import window from 'global/window';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import Page from 'components/Page/Page';
import PropTypes from 'prop-types';

import {
  onPageInsurance,
  setStateInsurance,
  onPageMore,
  setStateMore,
  onPageBusiness,
  setStateBusiness,
} from '../../actions/navActions';


// Footer
import FooterContainer from '../../containers/FooterContainer';

import './FairDealingStatement.scss';

class FairDealingStatement extends Component {
  constructor(props) {
    super(props);
    // reset indication
    const resetInsurance = onPageInsurance.NONE;
    const resetMore = onPageMore.NONE;
    const resetBusiness = onPageBusiness.NONE;
    const {
      resetIndicatorInsurance,
      resetIndicatorMore,
      resetIndicatorBusiness,
    } = this.props;
    resetIndicatorInsurance(resetInsurance);
    resetIndicatorMore(resetMore);
    resetIndicatorBusiness(resetBusiness);
  }
  render() {
    window.scrollTo(0, 0);
    return (
      <Page id="fairdealingstatement">
        <div className="fds">
          <div className="title-container">
            <div className="title">Our Commitment to Fair Dealing</div>
          </div>
          <div className="content">
            <div className="content-row">
              <div className="title-desc">
                At PolicyPal, we recognise the need to maintain the confidence that our
                customers have in us to deliver quality advice and products. To this end,
                we commit to operate at the highest standards to bolster the integrity and
                trust that our customers have in our business. The interest of our customers
                is a pertinent priority for us and thus, we commit to achieve the following
                fair dealing outcomes:
                <ol>
                  <li>
                    ensuring that fair dealing is central to our corporate culture;
                  </li>
                  <li>
                    ensuring that our customers have the confidence to deal with us;
                  </li>
                  <li>
                    ensuring that our products and services are suitable
                    for our target customer audience;
                  </li>
                  <li>
                    ensuring that customer feedback and complaints are handled in an independent,
                    effective and prompt manner to enable PolicyPal to continuously improve its services;
                  </li>
                  <li>
                    ensuring that we have competent and ethical representatives to provide our
                    customers with quality advice and appropriate recommendations; and
                  </li>
                  <li>
                    ensuring that our customers receive clear, relevant and timely information
                    to enable them to make informed financial decisions.
                  </li>
                </ol>
              </div>
              <div className="title-desc">
                We strive ceaselessly to enhance our customer experience here at PolicyPal.
                As such, we have taken active measures to ensure the delivery of the abovementioned
                fair dealing outcomes to our customers. For example, before a product is offered to our
                customers, we ensure that formal due diligence is conducted. This is to assess the risk-reward
                characteristics of the product as well as to fully understand its features so that we may
                properly advise our customers.
              </div>
              <div className="title-desc">
                Further, our representatives are trained, certified and competent to share their knowledge
                with customers in a fair and balanced way, to enable them to make informed financial decisions.
                Our staff also undergo continuous professional development and training to maintain the highest
                standards of service to our customers. Clear and relevant information, material developments and
                updates relating to the products are given in a fair and balanced manner, so that our customers
                are well-informed in order to protect their own interest.
              </div>
              <div className="title-desc">
                However, should our customers have any feedback or complaints, we have a robust process to
                resolve customer grievances independently, effectively and promptly. For any feedback,
                please email us at <a href="mailto:hello@policypal.com?subject=PolicyPal%20Fair%20Dealing%20Statement">hello@policypal.com</a>.
                As we are always looking to improve our service, we take feedback seriously here at PolicyPal.
              </div>
              <div className="title-desc">
                <i>Last updated: 29 January 2018</i>
              </div>
            </div>
          </div>
          <FooterContainer />
        </div>
      </Page>
    );
  }
}

FairDealingStatement.defaultProps = {
  resetIndicatorInsurance: null,
  resetIndicatorMore: null,
  resetIndicatorBusiness: null,
};
FairDealingStatement.propTypes = {
  resetIndicatorInsurance: PropTypes.func,
  resetIndicatorMore: PropTypes.func,
  resetIndicatorBusiness: PropTypes.func,
};

const mapDispatchToProps = dispatch => ({
  resetIndicatorInsurance: (resetInsurance) => {
    dispatch(setStateInsurance(resetInsurance));
  },
  resetIndicatorMore: (resetMore) => {
    dispatch(setStateMore(resetMore));
  },
  resetIndicatorBusiness: (resetBusiness) => {
    dispatch(setStateBusiness(resetBusiness));
  },
});

export default connect(null, mapDispatchToProps)(FairDealingStatement);

